.TodoItem{
  padding: .5em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}


.TodoItem .TitleWrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.TodoItem .TitleWrapper input{
  margin-left: .5em;
  margin-right: .5em;
}


.TodoItem>.title{
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 0 .5em;
}
.UserDialog-Wrapper{ 
  /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
  /*内容居中*/  display: -webkit-flex;  display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center;
  background: hsla(0, 0%, 0%, 0.4);
}
.UserDialog{
  border: 1px solid;
  border-radius: 4px;
  min-width: 20em;
  background: white;
  border: 1px solid #aaa;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em;  margin-left: -2em;}
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;}

.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }

.UserDialog .forgetPassword > h3{
  padding: 1em;
}
.UserDialog .forgetPassword > form{
  padding: 0 1em 1em;
}

.UserDialog form>.row{ display: -webkit-flex; display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; }
.UserDialog form>.row.actions{
  padding-left: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; 
}
.UserDialog form>.row.actions button{
  margin-right: .5em;
}

.UserDialog button{
  font: inherit;
  padding: .2em;
}
